import Behavior from '../Behavior';
import Status from '../Status';

/**
 * @class ExecuteScript
 * @extends module:jibo/bt.Behavior
 * @memberof module:jibo/bt/behaviors
 * @description
 * Executes arbitrary synchronous JavaScript.
 * @param {Object} options See {@link module:jibo/bt.Behavior|Behavior} for all options.
 * @param {Function} options.exec Function to execute.
 */
export default class ExecuteScript extends Behavior {
	start() {
		this.options.exec();
		return true;
	}
	update() {
		return Status.SUCCEEDED;
	}
}